<template>
    <div class="view-wrapper" :style="{height:pageMixin_browser.clientHeight} ">
        <div class="panel">
            <img class="top" src="../../assets/invitation.png" alt="">
            <div class="gold"></div>
            <div class="package">
                <div class="money">
                    待提现金额
                    <div class="dp-ellipsis"><b>
                        <DP_CountUp :decimals="2"
                                    :endnum="wallet.asset"
                                    class="count_up"
                                    duration="1.5"
                                    prefix=""
                                    separator=""
                                    suffix=""
                        ></DP_CountUp>
                    </b><span>元</span></div>
                    <div class="desc dp-ellipsis">已提现奖励：{{wallet.cumulativeWithdraw}}元</div>
                </div>
            </div>
            <button class="btn" @click="dp_go('account-center')">我要提现</button>

            <!--            <div class="tips dp-ellipsis">本期活动已结束 下次再来吧~</div>-->
            <div class="invite">
                <div class="title">简单四步，解锁好礼</div>
                <div class="content dp-ellipsis">
                    成功邀请 <b>
                    <DP_CountUp :decimals="0"
                                :endnum="friends"
                                class="count_up"
                                duration="1.5"
                                prefix=""
                                separator=""
                                suffix=""
                    ></DP_CountUp>
                </b> 人
                </div>
            </div>


        </div>


        <div v-show="this.userInfo.id" class="flotage">
            <DP_Button type="primary" @click.native="poster">继续邀请</DP_Button>
        </div>

    </div>
</template>

<script>
    import pageMixin from "@/mixin/pageMixin";
    import DP_CountUp from '@/components/DP_CountUp/index'
    import DP_Button from '@/components/DP_Button'
    import {mapGetters} from "vuex";
    import {getSuccessFriends} from "../../api/parameter";
    import {sernsorFn} from "@/utils/sensors";


    export default {
        metaInfo() {
            return {
                title: this.$route.meta.title || '熊猫博士', // set a title
                meta: [{                 // set meta
                    name: 'keywords',
                    content: this.$route.meta.title + ",熊猫博士"
                }],
            }
        },
        name: "invitation-detail",
        components: {
            DP_CountUp,
            DP_Button,
        },
        props: {},
        mixins: [pageMixin],
        data() {
            return {
                friends: 0,
            }
        },
        watch: {},
        computed: {
            ...mapGetters({
                wallet: "getWallet",
                pageParameter: "getPageParameter",
                userInfo: "getUserInfo",
            }),
        },
        methods: {
            async getFriends() {
                const res = await getSuccessFriends('com.drpanda.chineseacademy');
                const {data: {data, code}} = res;
                if (code === 0) {
                    this.friends = data.length;
                }
            },
            async poster() {
                sernsorFn('buttonClick', {
                    tabTitle: document.title,
                    moduleTitle: 'Referral',
                    buttonType: "普通按钮",
                    buttonName: "继续邀请"
                })

                const parameter = {
                    user_id:  this.userInfo.id,
                    avatar: this.userInfo.avatar,
                    name:this.userInfo.name,
                    sentence: this.pageParameter.posterAndShareContent.length > 0 ? this.pageParameter.posterAndShareContent[0].shareContent : '',
                    poster: this.pageParameter.posterAndShareContent.length > 0 ? this.pageParameter.posterAndShareContent[0].url : '',
                    bundleId: "com.drpanda.chineseacademy",
                };

                this.dp_go('poster2', {parameter: encodeURIComponent(JSON.stringify(parameter))});
            }
        },
        created() {
            this.$store.dispatch('getWallet');
            this.getFriends();

        },
        mounted() {
        },
    }
</script>

<style lang='scss' scoped>
    @import "../../assets/global/style/mixin";

    .view-wrapper {
        .panel {
            height: calc(100% - 240px);
            width: 100%;
            position: relative;
            background-color: #ffcd00;
            overflow: hidden;
            background-size: cover;
            @include animation_bg("../../assets/global/bg.png");
        }

        .top {
            width: 544px;
            height: 224px;
            position: absolute;
            top: 45px;
            left: 50%;
            margin-left: -272px;
        }

        .gold {
            background-image: url("../../assets/ca/amount/gold.png");
            background-repeat: no-repeat;
            background-size: 100% 100%;
            width: 1080px;
            height: 800px;
            position: absolute;
            top: 150px;
            left: 50%;
            margin-left: -540px;
            z-index: 4;
            animation: pulse 5s linear infinite;
        }

        .package {
            width: 686px;
            height: 669px;
            background-image: url("../../assets/ca/amount/package_bg.png");
            background-repeat: no-repeat;
            background-size: 100% 100%;
            position: absolute;
            top: 350px;
            left: 50%;
            z-index: 3;
            margin-left: -343px;


            .money {
                font-size: 44px;
                color: #692a2a;
                font-weight: bold;
                width: 500px;
                text-align: center;
                margin: 0 auto;
                margin-top: 50px;

                b {
                    font-size: 125px;
                    color: #ff4438;
                }

                span {
                    color: #ff4438;
                }

                .desc {
                    font-weight: normal;
                    font-size: 36px;
                    margin-top: 30px;
                }
            }


        }

        .btn {
            background-color: transparent;
            border: 2px solid rgba(255, 255, 255, 0.81);
            font-size: 50px;
            color: white;
            padding: 25px 60px;
            border-radius: 30px;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            top: 850px;
            z-index: 99;
        }

        .invite {
            border-radius: 30px;
            box-sizing: border-box;
            margin: 90px auto;
            margin-top: 150px;
            width: 924px;
            background-color: white;
            color: #6e6e6e;
            font-size: 39px;
            padding: 130px 70px 70px 70px;
            position: absolute;
            z-index: 1;
            top: 1050px;
            left: 50%;
            margin-left: -462px;

            .title {
                background-color: #fff0b4;
                color: #692a2a;
                width: 540px;
                height: 92px;
                line-height: 92px;
                text-align: center;
                border-radius: 30px;
                font-weight: bold;
                margin: 0 auto;
                margin-top: -200px;


            }

            .content {
                font-size: 50px;
                font-weight: bold;
                color: #692a2a;
                background-color: transparent;
                text-align: center;
                margin-top: 60px;

                b {
                    font-size: 70px;
                    color: #c54242;
                }
            }
        }

        .tips {
            font-size: 44px;
            position: absolute;
            text-align: center;
            width: 100%;
            bottom: 550px;
            color: #692a2a;
        }

        .flotage {
            z-index: 10;
            width: 1080px;
            height: 240px;
            background-color: #ffffff;
            position: fixed;
            bottom: 0;
            left: 0;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;

            button {
                background-color: #fd601a;
                color: white;
                width: 570px;
                height: 150px;
                line-height: 150px;
                border: none;
                border-radius: 75px;
                font-size: 54px;
            }
        }
    }
</style>
